import React, { useState, useEffect } from 'react';
import { Link, Route } from "react-router-dom";
import { ethers } from 'ethers';
import { network } from '../../../data/constants';
import NumberFormat from 'react-number-format';
import { TotalSupply, CirculatingSupply } from '../../../components';
import ProductsData from '../../../data/products.json';
import { Table, Badge } from 'antd';
import Product from './Product/Product';

import { ReactComponent as IconInr } from '../../../components/Assets/Images/icon_inr.svg';
import { ReactComponent as IconEs } from '../../../components/Assets/Images/icon_es.svg';
import { ReactComponent as IconEi } from '../../../components/Assets/Images/icon_ei.svg';
import { ReactComponent as IconGp } from '../../../components/Assets/Images/icon_gp.svg';
import { ReactComponent as IconSp } from '../../../components/Assets/Images/icon_sp.svg';

import './Products.scss';

enum PoolStatus {
  'Active' = 'Active',
  'Inactive' = 'Inactive',
  'Finished' = 'Finished',
}

const RenderImage = (image) => {
  switch(image) {
    case "INR": return <IconInr className="PoolItem__icon" />;
    case "GP": return <IconGp className="PoolItem__icon" />;
    case "ES": return <IconEs className="PoolItem__icon" />;
    case "EI": return <IconEi className="PoolItem__icon" />;
    case "SP": return <IconSp className="PoolItem__icon" />;
    default: return <IconInr className="PoolItem__icon" />;
  }
}

const RenderBadge = (status) => {
  switch(PoolStatus[status]) {
    case PoolStatus.Active: return <Badge color="green" />;
    case PoolStatus.Inactive: return <Badge color="red" />;
    case PoolStatus.Finished: return<Badge color="orange" />;
  }
}


const Products = ({ url }) => {
  const columns = [
    {
      key: 'image',
      render: (record) => RenderImage(record.image)
    },
    {
      title: 'Product',
      key: 'product',
      render: (record) => 
        <div>
          <div>{record.projectName}</div>
          <div><a href={record.address} rel="noreferrer" target="_blank">{record.tokenName}</a></div>
          <div>{record.description}</div>
        </div>
    },
    {
      title: 'Supply',
      key: 'supply',
      render: (record) => {
        if (!record.contract) {
          return (
            <>
              {"-"}/{"-"}
            </>
          )
        }
        return (
          <>
            <CirculatingSupply contract={record.contract} provider={provider} decimals={0} pools={record.pools} />
            /<TotalSupply contract={record.contract} provider={provider} decimals={0} />
          </>
        )
      }
    },
    {
      title: 'Market Cap',
      key: 'cap',
      render: (record) => <NumberFormat value={10000000} displayType={'text'} thousandSeparator={true} prefix="$" />
    },
    {
      title: 'Status',
      key: 'status',
      render: (record) => 
        <div>
          {RenderBadge(record.status)} {record.status}
        </div>
    },
    {
      title: 'Action',
      key: 'action',
      render: (text, record) => (
        <Link to={`/lab/products/${record.id}`}>View</Link>
      ),
    },
  ];

  const [provider, setProvider]: [any | null, any] = useState(null);

  useEffect(() => {
		async function initProvider() {
			const provider = new ethers.providers.EtherscanProvider(network, 'WSEBKEYQAFZ8AUGMFAKJR7GPCNYZ9Q3AIE');
			setProvider(provider);
		}
		initProvider();
  }, []);

  return (
    <div className="Products">
      {provider && <Table columns={columns} dataSource={ProductsData} pagination={false} rowKey="tokenName" />}
      <Route exact path={`${url}/products/:productId`}>
        {provider && <Product provider={provider} /> }
      </Route>
    </div>
  );
}

export default Products;